"use client";
import { useState, useRef, useEffect } from "react";
import { ChevronDown } from "lucide-react";
import ProjectCard from "../ui/ProjectCard";

export default function Projects() {
  const [visibleCount, setVisibleCount] = useState(3);
  const projectsRef = useRef(null);

  const handleShowMore = () => {
    setVisibleCount((prev) => Math.min(prev + 3, projectsData.length));
    setTimeout(() => {
      projectsRef.current?.scrollIntoView({ behavior: "smooth", block: "start" });
    }, 200);
  };

  const handleShowLess = () => {
    setVisibleCount(3);
    setTimeout(() => {
      projectsRef.current?.scrollIntoView({ behavior: "smooth", block: "start" });
    }, 200);
  };

  return (
    <section
      id="projects"
      ref={projectsRef}
      className="py-24 bg-white dark:bg-gray-800"
    >
      <div className="container mx-auto px-6">
        <h2 className="text-4xl font-bold text-center mb-16 relative">
          Projects
          <div className="w-24 h-1 bg-blue-500 mx-auto mt-4"></div>
        </h2>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-10">
          {visibleProjects.map((project, index) => (
            <div
              key={index}
              className="animate-fadeIn opacity-0"
              style={{
                animationDelay: `${index * 100}ms`,
                animationFillMode: "forwards",
                animationDuration: "0.6s",
              }}
            >
              <ProjectCard project={project} />
            </div>
          ))}
        </div>

        <div className="flex justify-center mt-12">
          {!allProjectsVisible ? (
            <button
              onClick={handleShowMore}
              className="flex items-center gap-2 bg-blue-600 hover:bg-blue-700 text-white px-6 py-3 rounded-lg
font-medium transition-all duration-300 hover:scale-105 relative overflow-hidden group hover:shadow-glow"
            >
              <span className="relative z-10">Show More Projects</span>
              <span
                className="relative z-10"
                aria-label="Show more projects"
              >
                <ChevronDown />
              </span>
            </button>
          ) : (
            <button
              onClick={handleShowLess}
              className="flex items-center gap-2 bg-blue-600 hover:bg-blue-700 text-white px-6 py-3 rounded-lg
font-medium transition-all duration-300 hover:scale-105 relative overflow-hidden group hover:shadow-glow"
            >
              <span className="relative z-10">Show Less Projects</span>
              <span
                className="relative z-10"
                aria-label="Show less projects"
              >
                <ChevronDown />
              </span>
            </button>
          )}
        </div>
      </div>
    </section>
  );
}
